@import 'src/common/mixin';
@import 'src/common/variables';

.mainBlock{
    height: 300px;
    width: 250px;
    border: 2px solid $mainColor;
    border-radius: 10px;
    display: flex;
    padding: 5px;
    @include flex (flex,column, space-between, center);

    .picBlock{
        height: 60%;
        width: 95%;
        border-radius: 10px;
        img{
            height: 100%;
            width: 100%;
            border-radius: 10px;
            object-fit: cover;
        }
    }
    h3{
        font-size: 23px;
    }
    .buttonBlock{
        width: 120px;
        height: 30px;
        padding: 0 10px;
        font-size: 16px;
        font-weight: bold;
        color: $mainTextColor;
        border-radius: 7px;
        background-color: $mainColor;
        cursor: pointer;
        transition: 0.3s;
    }
    .buttonBlock:hover{
        transform: scale(1.1, 1.1);
    }
    .buttonBlock:active{
        background-color: #a01dfa;
    }
}


